.console-warp {
    position: absolute;
    top: 0;


}

.console-drawer {
    .ant-drawer-header {
        padding: 10px 24px;
    }

    .ant-drawer-body {
        display: flex;
        padding: 10px;

        .operation {
            height: 100%;
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            overflow: auto;
            .slider-warp {
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 15px;
                font-weight: 500;
                .slider {
                    width: 210px;
                }
            }

            .btn-group {
                .row {
                    display: flex;
                    justify-content: center;

                    button {
                        margin: 5px 5px;
                    }
                }
            }

            .input-group {
                display: flex;
                justify-content: center;

                label {
                    display: flex;
                    align-items: center;
                    margin-right: 10px;

                    .lable-name {
                        font-weight: bold;
                        color: rgb(103, 101, 101);
                        height: 32px;
                        width: 40px;
                        text-align: center;
                        background-color: #FAFAFA;
                        font-size: 14px;
                        line-height: 32px;
                        border: #d9d9d9 1px solid;
                        border-radius: 2px;
                        display: inline-block;
                    }
                }

                button {
                    margin-right: 5px;
                }

            }
        }

        .displayer {
            padding: 5px 25px;
            border-left: 2px solid rgb(218, 215, 215);
            height: 100%;
            flex: 1;

            .content {
                border-radius: 3px;
                padding: 10px 20px;
                height: 100%;
                border: rgb(211, 208, 208) 1px solid;
                overflow: auto;
                font-family: Monaco, Menlo, Ubuntu Mono, Consolas, source-code-pro, monospace;
            }
        }
    }
}